.main {
  text-align: center;
  display: flex;
  justify-content: center;
  flex-direction: row; }

.item {
  margin: 10px;
  width: 50%;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0; }


.Favorites {
  text-align: center;
  border: solid red;
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 90%; }

.button {
  display: flex;
  align-items: center;
  justify-content: start;
  flex-direction: row;
  margin-top: 30px;
  padding: 0; }

.card {
  width: 80%;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  margin: 20px;
  background-color: white; }


.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2); }


.list {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 30px;
  padding: 0; }
